import React from "react";
import styled, { css, injectGlobal } from "styled-components";
import Helmet from "react-helmet";
import BgImage from "../components/BgImage";
import InfoCard from "../components/InfoCard";

injectGlobal`
  body {
    background: linear-gradient(
      to bottom,
      rgb(87, 111, 156) 0%,
      rgb(42, 60, 92) 100%
    );
    margin: 0;
    padding: 0;

    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 14px;
  }
`;

const topCss = css`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
`;

const Background = styled(BgImage)`
  ${topCss};
`;

const Foo = styled.div`
  ${topCss};
  background-color: rgba(42, 60, 92, 0.7);
  mix-blend-mode: multiply;
`;

const StyledApp = styled.div`
  ${topCss};
  font-size: 28px;
  height: 100vh;
  min-height: 550px;
  z-index: 1;
`;

const Index = ({ data }) => {
  const { title, author, description, url, keywords } = data.site.siteMetadata;

  const image = data.file.publicURL;

  const fluidImage = data.file.childImageSharp.fluid;

  return (
    <div>
      <Helmet title={title}>
        {/* General tags */}
        <meta name="description" content={description} />
        <meta name="author" content={author} />
        <meta name="keywords" content={keywords} />
        <meta name="description" content={description} />
        {/*
        {/* OpenGraph tags */}
        <meta property="og:url" content={url} />
        <meta property="og:title" content={title} />
        <meta property="og:description" content={description} />
        <meta property="og:image" content={image} />
      </Helmet>
      <Background fluid={fluidImage} />
      <Foo />
      <StyledApp>
        <InfoCard />
      </StyledApp>
    </div>
  );
};
export default Index;

export const indexQuery = graphql`
  query IndexQuery {
    site {
      siteMetadata {
        title
        author
        description
        url
        keywords
      }
    }
    file(relativePath: { eq: "kc.jpg" }) {
      publicURL
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`;
